"use client";

import React from "react";
import Link from "next/link";
import { Button } from "antd";

const Navbar = () => {
  return (
    <nav className="bg-white border-gray-200 py-2.5 dark:bg-gray-900">
      <div className="flex flex-wrap items-center justify-between max-w-screen-xl px-4 mx-auto">
        {/* Logo */}
        <Link href="/" className="flex items-center">
          <span className="self-center text-xl font-semibold whitespace-nowrap dark:text-white">
            Company Logo
          </span>
        </Link>

        {/* Right Section */}
        <div className="flex items-center lg:order-2">
          <Button
            type="primary"
            className="bg-purple-700 hover:bg-purple-800"
          >
            Download
          </Button>
        </div>

        {/* Menu */}
        <div
          className="items-center justify-between w-full lg:flex lg:w-auto lg:order-1"
          id="mobile-menu-2"
        >
          <ul className="flex flex-col mt-4 font-medium lg:flex-row lg:space-x-8 lg:mt-0">
            <li>
              <Link
                href="/"
                className="block py-2 pl-3 pr-4 text-white bg-purple-700 rounded 
                lg:bg-transparent lg:text-purple-700 lg:p-0 dark:text-white"
              >
                Home
              </Link>
            </li>
            <li>
              <Link
                href="/company"
                className="block py-2 pl-3 pr-4 text-gray-700 border-b border-gray-100 hover:bg-gray-50 
                lg:hover:bg-transparent lg:border-0 lg:hover:text-purple-700 lg:p-0 
                dark:text-gray-400 lg:dark:hover:text-white dark:hover:bg-gray-700 
                dark:hover:text-white lg:dark:hover:bg-transparent dark:border-gray-700"
              >
                Company
              </Link>
            </li>
            <li>
              <Link
                href="/contact"
                className="block py-2 pl-3 pr-4 text-gray-700 border-b border-gray-100 hover:bg-gray-50 
                lg:hover:bg-transparent lg:border-0 lg:hover:text-purple-700 lg:p-0 
                dark:text-gray-400 lg:dark:hover:text-white dark:hover:bg-gray-700 
                dark:hover:text-white lg:dark:hover:bg-transparent dark:border-gray-700"
              >
                Contact
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
